<template>
  <iframe :src="iframUrl" frameborder="0"></iframe>
</template>

<script>
export default {
  name: "Other",
  data() {
    return {
      adsUrl: [
        "https://www.baidu.com",
        "https://www.163.com",
        "https://www.qq.com",
      ],
      iframUrl: "",
    };
  },
  created() {
    let random = Math.random();
    let index = random < 0.3 ? 0 : random < 0.6 ? 1 : 2;
    this.iframUrl = this.adsUrl[index];
  },
};
</script>

